import uniIcons from '../uni-icons/uni-icons.vue'

/**
 * Steps 步骤条
 * @description 评分组件
 * @tutorial https://ext.dcloud.net.cn/plugin?id=34
 * @property {Number} active 当前步骤
 * @property {String} direction = [row|column] 当前步骤
 * 	@value row 横向
 * 	@value column 纵向
 * @property {String} activeColor 选中状态的颜色
 * @property {Array} options 数据源，格式为：[{title:'xxx',desc:'xxx'},{title:'xxx',desc:'xxx'}]
 */

export default {
	name: 'UniSteps',
	components: {
		uniIcons
	},
	props: {
		direction: {
			// 排列方向 row column
			type: String,
			default: 'row'
		},
		activeColor: {
			// 激活状态颜色
			type: String,
			default: '#1aad19'
		},
		deactiveColor: {
			// 未激活状态颜色
			type: String,
			default: '#999999'
		},
		active: {
			// 当前步骤
			type: Number,
			default: 0
		},
		options: {
			type: Array,
			default () {
				return []
			}
		} // 数据
	},
	data() {
		return {}
	}
}
